.glist{
    margin-top: 55px;
    li{
        width: 90%;
        height: 80px;
        background: #f0f0f0;
        position: relative;
        margin: 10px 5%;
        .am-checkbox-wrapper{
            position: absolute;
            left: 2%;
            top: 36%;
        }
        img{
            height: 100%;
            position: absolute;
            left: 10%;
        }
        h3{
            position: absolute;
            left: 40%;
            top: 30%;
            font-size: 13px;
        }
        p{
            position: absolute;
            left: 40%;
            bottom: 10%;
            color: #e90404;
            font-size: 10px;
        }
        span{
            color: #323232;
            width: 21px;
            height: 21px;
            text-align: center;
            margin: 0 3px;
            line-height: 21px;
        }
        .jianNum{
            display: inline-block;
            color:#cacaca;
            width: 21px;
            height: 21px;
            border: 1px solid #cacaca;
            position: absolute;
            top: 40%;
            right: 15%;
        }
        .num{
            position: absolute;
            top: 40%;
            right: 10%;
        }
        .jiaNum{
            display: inline-block;
            color:#cacaca;
            width: 21px;
            height: 21px;
            border: 1px solid #cacaca;
            position: absolute;
            top: 40%;
            right: 3%;
        }
    }
}

.shopFoot{
    width: 95%;
    height: 40px;
    position: fixed;
    left: 5%;
    bottom: 45px;
    display: flex;
    justify-content:space-between;
    align-items: center;
    .am-checkbox-input{
        display:inline-block
    }
    p{
        width: 120px;
        text-align: center;
        span{
            color: #e90404;
        }
    }
    p:nth-last-child(1){
        height: 40px;
        line-height: 40px;
        border-radius: 10px;
        background: #e90404;
        color: #fff;
    }
}